@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@400;700&display=swap");
.container {
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  max-width: 770px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -20px;
  margin-left: -20px;
}

.col {
  padding-right: 20px;
  padding-left: 20px;
  flex: 0 0 50%;
  max-width: 50%;
}

.marks {
  display: flex;
}

.marks .mark {
  display: block;
}

.marks .mark:not(:first-of-type) {
  margin-left: 8px;
}

.mark {
  padding: 5px 10px;
  font-size: 14px;
  border-radius: 50px;
  cursor: default;
}

.mark_info {
  padding-right: 32px;
  position: relative;
}

.mark_info::before {
  position: absolute;
  content: '?';
  top: calc(50% - 7px);
  right: 10px;
  width: 14px;
  height: 14px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  box-sizing: border-box;
  cursor: pointer;
}

.mark_orange {
  color: #F2994A;
  background: #FFF7F2;
}

.mark_orange::before {
  border: 1px solid #F2994A;
}

.mark_violet {
  background: #E1E0FC;
  color: #9796D0;
}

.mark_violet::before {
  border: 1px solid #9796D0;
}

.mark_green {
  color: #56B77F;
  background: #DDFFE2;
}

.mark_green::before {
  border: 1px solid #56B77F;
}

.btn {
  background: #F1F5F9;
  border: none;
  color: #1F1F25;
  border-radius: 8px;
  padding: 14px 58px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}

.btn:hover {
  background: #E5EFF9;
}

.btn:active {
  background: #EAF1F9;
}

.btn:disabled {
  opacity: 0.5;
}

.btn_yellow {
  background: #FFD400;
  border: 1px solid #FFD400;
}

.btn_yellow:hover {
  background: #FDCA21;
  border: 1px solid #FDCA21;
}

.btn_yellow:active {
  background: #FFD138;
  border: 1px solid #FFD138;
}

.input-group {
  display: flex;
  flex-direction: column;
}

.input-group label {
  font-size: 14px;
  color: #79818C;
  margin-bottom: 8px;
}

.myInput {
  background: #FFFFFF;
  border: 1px solid #D8D8D8;
  box-sizing: border-box;
  border-radius: 8px;
  padding: 12px 16px;
  font-size: 16px;
  color: #1F1F25;
}

.myInput::placeholder {
  color: #79818C;
}

.myInput:not(:placeholder-shown) {
  border: 1px solid #818181;
}

.myInput:disabled {
  background: #F1F5F9;
  border: 1px solid #F1F5F9;
  color: #9BA3AE;
  cursor: not-allowed;
}

.myInput:hover, .myInput.hover {
  border: 1px solid #79818C;
}

.myInput:focus, .myInput:active, .myInput.active {
  outline: none;
  border: 1px solid #367EE7;
}

.myCheckbox {
  cursor: pointer;
  position: relative;
}

.myCheckbox input {
  width: 0;
  height: 0;
  opacity: 0;
  position: absolute;
}

.myCheckbox span {
  padding: 8px 16px;
  border: 1px solid #D8D8D8;
  border-radius: 40px;
  display: inline-block;
}

.myCheckbox span:hover {
  border: 1px solid #1F1F25;
}

.myCheckbox input:checked + span {
  background: #FFD400;
  border: 1px solid #FFD400;
}

.myCheckbox input:checked + span:hover {
  background: #FDCA21;
  border: 1px solid #FDCA21;
}

.myCheckbox input:checked + span:active {
  background: #FFD138;
  border: 1px solid #FFD138;
}

.myCheckbox input:checked:disabled + span {
  opacity: 0.5;
  cursor: not-allowed;
}

.input-filters__label {
  font-size: 14px;
  color: #79818C;
  margin-bottom: 8px;
}

.input-filters__inputs {
  display: flex;
  position: relative;
}

.input-filters__inputs::before {
  content: '';
  position: absolute;
  width: 1px;
  height: 24px;
  background: #D8D8D8;
  top: calc(50% - 12px);
  left: 50%;
  z-index: 100;
}

.input-filters__unit {
  position: absolute;
  width: 1px;
  height: 24px;
  color: #79818C;
  top: calc(50% - 12px);
  right: calc(11px + 16px);
  z-index: 100;
}

.input-filters label {
  position: absolute;
  font-size: 16px;
  line-height: 16px;
  top: calc(50% - 8px);
  left: 16px;
  z-index: 100;
  color: #79818C;
}

.input-filters label:last-of-type {
  left: calc(50% + 8px);
}

.input-filters input {
  background: #FFFFFF;
  border: 1px solid #D8D8D8;
  box-sizing: border-box;
  border-radius: 8px;
  padding: 12px 16px;
  font-size: 16px;
  color: #1F1F25;
  display: block;
  width: 50%;
}

.input-filters input::placeholder {
  color: #79818C;
}

.input-filters input:not(:placeholder-shown) {
  border: 1px solid #818181;
}

.input-filters input:disabled {
  background: #F1F5F9;
  border: 1px solid #F1F5F9;
  color: #9BA3AE;
  cursor: not-allowed;
}

.input-filters input:hover, .input-filters input.hover {
  border: 1px solid #79818C;
}

.input-filters input:focus, .input-filters input:active, .input-filters input.active {
  outline: none;
  border: 1px solid #367EE7;
}

.input-filters input::placeholder {
  font-size: 16px;
}

.input-filters input:first-of-type {
  padding-left: 45px;
  padding-right: 45px;
  border-right: none;
  border-radius: 8px 0 0 8px;
}

.input-filters input:last-of-type {
  padding-left: 35px;
  padding-right: 35px;
  border-left: none;
  border-radius: 0 8px 8px 0;
}

.window {
  border: 1px solid #D8D8D8;
  background: #FFFFFF;
  border-radius: 10px;
  padding: 30px;
}

.filters {
  margin-bottom: 32px;
}

.filters__group {
  width: 100%;
}

.filters__group:not(:first-of-type) {
  margin-left: 30px;
}

.filters__group-label {
  font-size: 14px;
  color: #79818C;
  margin-bottom: 8px;
}

.filters__group-content label:not(:last-of-type) {
  margin-right: 8px;
}

.filters__line {
  display: flex;
  flex: 1;
  margin-bottom: 20px;
}

.filters__line:last-of-type {
  margin-bottom: 30px;
}

.filters__title {
  margin-bottom: 32px;
}

.filters__btns {
  display: flex;
  justify-content: space-between;
}

.result {
  padding-right: 0;
  padding-left: 0;
  padding-bottom: 0;
}

.result__title {
  padding-left: 30px;
  padding-right: 30px;
}

.table * {
  transition: all 0.3s;
}

.table__content {
  padding: 0 30px;
  max-height: 0px;
  overflow: hidden;
  box-sizing: border-box;
}

.table__content .marks,
.table__content .card-apart {
  margin-bottom: 20px;
}

.table__content .card-apart {
  position: relative;
}

.table__content .card-apart::before {
  position: absolute;
  content: '';
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: #79818C;
}

.table__line {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  padding: 15px 0 17px;
}

.table__row.open {
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.12);
}

.table__row.open .table__content {
  max-height: 600px;
  padding: 30px;
}

.table__row.open .table__line::before {
  content: none;
}

.table__td.bold {
  font-weight: bold;
}

.table__td .rotate {
  transform: rotate(180deg);
}

.table__td.icons {
  display: flex;
}

.table__td.icons span {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 40px;
  cursor: pointer;
}

.table__td.icons span:first-of-type {
  background: #E5EFF9;
}

.table__th, .table__td {
  flex: 1;
  justify-content: space-between;
}

.table__th:last-child, .table__td:last-child {
  max-width: 80px;
}

.table__title {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  padding: 16px;
}

.table__title, .table__line {
  padding-left: 30px;
  padding-right: 30px;
  position: relative;
}

.table__title::before, .table__line::before {
  position: absolute;
  content: '';
  bottom: 0;
  left: 4%;
  width: 92%;
  height: 1px;
  background: #F1F5F9;
}

.table__title:hover, .table__line:hover {
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.12);
}

.card-apart {
  display: flex;
  align-items: center;
}

.card-apart__content {
  padding: 24px 0 27px 12px;
}

.card-apart__deatail-title {
  color: #79818C;
  font-size: 14px;
  margin-bottom: 4px;
}

.card-apart__deatail-val {
  font-size: 16px;
}

.card-apart__detail:not(:first-of-type) {
  margin-left: 60px;
}

.card-apart__details {
  display: flex;
  margin-bottom: 15px;
}

.card-apart__img {
  padding: 0 16px;
}

.card-apart__img img {
  max-width: 100%;
}

.card-apart__link {
  color: #367EE7;
  font-weight: bold;
  font-size: 16px;
}

.card-apart__tag {
  font-size: 14px;
  margin-bottom: 20px;
}

.card-apart__tag:not(:first-of-type) {
  margin-left: 32px;
  position: relative;
}

.card-apart__tag:not(:first-of-type)::before {
  position: absolute;
  content: '';
  top: 0;
  left: -16px;
  width: 1px;
  bottom: 0;
  background: #D8D8D8;
}

.card-apart__tags {
  display: flex;
}

.card-apart__title {
  font-weight: bold;
  font-size: 24px;
  margin-bottom: 16px;
}

.callback__desc {
  font-size: 14px;
  margin-bottom: 20px;
}

.callback__line {
  display: flex;
  flex: 1;
  align-items: flex-end;
  margin-bottom: 12px;
}

.callback__line > *:not(:first-child) {
  margin-left: 12px;
}

.callback__line .btn {
  padding-right: 0;
  padding-left: 0;
  width: 100%;
}

.callback__polit {
  font-size: 14px;
  color: #79818C;
}

.callback__polit a {
  color: #367EE7;
}

.callback__title {
  font-size: 20px;
  margin-bottom: 8px;
}

.empty {
  padding: 30px;
  display: flex;
}

.empty__content {
  margin-left: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 280px;
}

.empty__desc {
  font-size: 16px;
}

.empty__title {
  font-weight: bold;
  font-size: 24px;
  margin-bottom: 12px;
}

* {
  padding: 0;
  margin: 0;
  transition: all 0.2s;
  box-sizing: border-box;
}

html {
  font-size: 18px;
  scroll-behavior: smooth;
  background: #E5E5E5;
  font-family: 'Rubik', sans-serif;
  color: #1F1F25;
}

body {
  margin: 80px 0;
}

img {
  display: block;
}

a {
  text-decoration: none;
  color: inherit;
}

ul li {
  list-style: none;
}

.h1 {
  font-size: 34px;
}

.h2 {
  font-size: 24px;
  margin-bottom: 24px;
}
